/*-------------------------------Blog posts page header-----------------------------*/
/*Header image*/
.coverPhoto {
    background-image: url('/blog-header.png');
    background-position: center bottom 40%;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    max-width: 100%;
    overflow: hidden;
    position: relative;
  }
  
/*Heading title*/
.title h1 {
    font-family: 'Playfair Display', serif;
    font-size: 5vw;
    letter-spacing: 8px;
    margin-top: 26vw;
    text-transform: uppercase;
    text-align: center;
  }

/*-------------------------------Blog post Preview-----------------------------*/
/*Author circle crop*/

.preview-pic {
    width: 35.807vw;
    height: 22.786vw;
}

.author-pic {
    border-radius: 50%;
    height: 80px;
    overflow: hidden;
    width: 80px;
    margin-right: 1.5vw;
    margin-left: 0px;
}

/*Author image*/
.vivw_img {
    height: auto;
    transform: translate( -5%, -10%);
}

/*Post preview*/
.blog-details {
    margin-bottom: 10px;
}

.heading {
    color: #FEB14B;
    letter-spacing: 0.1vw;
    font-size: 11pt;
    font-family: 'Montserrat', sans-serif;
    text-decoration: none;
    margin-bottom: 10px;
}

.auth {
    color: #FEB14B;
    letter-spacing: 0.1vw;
    font-size: 10pt;
    font-family: 'Montserrat', sans-serif;
    text-decoration: none;
    margin-bottom: 10px;
}

.heading:hover {
    text-decoration: none;
}

.subheading {
    color: #707070;
    letter-spacing: 0.1vw;
    font-size: 9pt;
    font-family: 'Montserrat', sans-serif;
    font-weight: 300;
    padding: 0;
    margin-bottom: 10px;
}

.date {
    color: #313638;
    letter-spacing: 0.1vw;
    font-size: 7pt;
    font-family: 'Montserrat', sans-serif;
    padding: 0;
    margin: 0;
}

table {
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    margin: 0 auto;
    width: 70vw;
    height: 100%;
}

.blog-preview {
    padding: 30px;  
}

@media screen and (max-width: 1000px) {
    table {
        width: 90%;
    }
    
    .author-pic {
        width: 50px;
        height: 50px;
    }
}

@media screen and (max-width: 800px) {
    .blog-preview {
        padding: 20px;
    }

    tr {
        display: flex;
        flex-wrap: wrap;
        width: 100%;
    }
    
    td {
        display: block;
        width: 100%;
    }
    
    table {
        width: 95%;
    }

    .preview-pic {
        width: 100%;
        height: auto;
    }
}