@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300;400;500;600&family=Playfair+Display&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/*font-family: 'Playfair Display', serif;
font-family: 'Montserrat', sans-serif;*/

h1 {
    color: #feb14b;
    font-family: 'Playfair Display', serif;
    font-size: 48pt;
}

h2 {
    color: #feb14b;
    font-family: 'Montserrat', sans-serif;
    font-size: 30pt;
    text-transform: uppercase;
    padding-bottom: 20px;
    text-align: center;
}

.header{
    padding-top: 50px;
    padding-bottom: 20px;
    text-align: center;
}

.subheader {
    font-family: 'Montserrat', sans-serif;
    font-size: 13pt;
    color: grey;
}

p {
    font-family: 'Montserrat', sans-serif;
    font-size: 10pt;
    margin-top: 0;
}

.body_bold {
    font-family: 'Montserrat', sans-serif;
    font-size: 10pt;
    font-weight: bold;
}

.centre {
    text-align: center;
}

.button {
    font-family: 'Montserrat', sans-serif;
    background-color:#e85f5c;
    border-radius: 60px;
    border-style: none;
    color: white;
    padding: 10px 45px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    cursor: pointer;
}

.button:hover {
    background-color: #feb14b;
    color: #ffffff;
}

/*-----------------------------------Navbar-----------------------------------*/
.bg-custom{
    background: #fff7ef;
    font-size: 10pt;
    padding: 0px;
}

.nav{
    height: 70px;
}

.navbar-nav{
    color: #313638;
    font-family: 'Montserrat', sans-serif;
    text-align: center;
    text-decoration: none;
}

.nav-item{
    float:right;
    color: #313638;
    padding: 20px 5px;
    text-decoration: none;
    letter-spacing: 1.5px;
}

.nav-item-btn {
    color: #313638;
    padding-top: 22px;
    padding-right: 10px;
    display: none;
}

.nav-item a{
    color: #313638;
    padding: 10px 15px;
    text-decoration: none;
}

.dropdown-menu{
    border-color: transparent;
    letter-spacing: 1.5px;
    font-size: 14px;
    background-color: #fff7ef;
    margin-top: 15px;
    border-radius: 0px;
}

.dropdown-item {
    padding: 5px 0px;
}

.dropdown-item:hover {
    background-color: #fff7ef;
}

.menuTitle{
    color: #313638; 
    padding: 10px 15px;
}

.menuTitle:hover{
    color: #e85f5c;
}

.nav-item a:hover{
    color: #e85f5c;
    padding: 10px 15px;
    text-decoration: none;
}


@media screen and (max-width: 1000px) {
    .nav-item {
        display: none;
    } 

    .nav-item-btn {
        display: block;
    }
}

/*-----------------------------------Menu-----------------------------------*/

.menu-container {
    position: fixed;
    width: 100%;
    background: #fff7ef;
    opacity: 1;
    color: #313638;
    z-index: 1;
    box-shadow: 3px 3px 5px 0 rgba(0, 0, 0, 0.2);
}

.menu-item {
    color: #313638;
    padding: 5px 35px;
    text-decoration: none;
    letter-spacing: 1.5px;
    text-transform: capitalize;
    cursor: pointer;
    font-size: 16px;
}

.menu-box:hover {
    width: 92%;
    background-color: #fff0e1c1;
    border-radius: 5px;
    margin-left: auto;
    margin-right: auto;
}

.menu-box:hover .menu-item {
    color: #e85f5c;
}

.menu-bar {
    float: none;
}

.menu-list {
    padding-top: 4rem;
}

.menu-dropdown {
    padding: 5px 0px;
}

/*----------------------------------Footer------------------------------------*/
footer {
    margin: 0px;
    padding: 0px;
    background-color: #fff7ef;
    min-width: 100%;
}

.footer-all {
    list-style-type: none;
    margin: 1.4vw auto;
    padding: 4vw 0vw 2vw 0vw;
}

.footer-item {
    font-family: 'Montserrat', sans-serif;
    padding: 0.33vw;
    color: #313638;
    text-transform: uppercase;
    font-size: 1.05vw;
}

.bottom-footer-item {
    font-family: 'Montserrat', sans-serif;
    padding: 0.3vw;
    color: #313638;
    text-transform: uppercase;
}

.footer-logo {
    margin-top: 1%; 
}

.footer-icons {
    padding-right:1vw;
}

.col-width {
    flex-basis: 40%;
}

.socials {
    margin-left: 8%;
}

.footer-copyright {
    font-family: 'Montserrat', sans-serif;
    text-transform: none;
    padding-bottom: 1vw;
    font-size: 1.05vw;
}

.footer-copyright, a {
    color: #313638;
}

.footer-item a:hover {
    color: #feb14b;
    text-decoration: none;
}

.bottom-footer-item a:hover {
    color: #feb14b;
    text-decoration: none;
}

.grid-footer-container {
    display: flex;
    width: 70%;
    margin: 0px auto;
}

@media screen and (max-width: 600px) {
    .grid-footer-container {
        flex-wrap: wrap;
        flex-direction: column;
        align-items: center;
        width: 100%;
    }

    .footer-item {
        text-align: center;
        font-size: 16px;
    }
    
    .footer-copyright {
        font-size: 16px;
    }

    .footer-logo {
        margin-bottom: 2%;
    }

    .socials {
        margin-left: 0%;
        margin-top: 1%;
    }

    .footer-all {
        margin: 1.5vw auto 1vw auto;
    }
    
}

/*-------------------------------Home page header-----------------------------*/
/*Header image*/
.contain {
    background-image: url('/header-1.png');
    background-position: center bottom 40%;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    max-width: 100%;
    background-color: #313638;
}

/*Header title*/
div .headline{
    text-align: left;
    position: absolute;
    top: 30%;
    left: 54vw;
}

.headline h1 {
    color: #feb14b;
    font-family: 'Playfair Display', serif;
    font-size: 5.5vw;
    text-transform: uppercase;
    letter-spacing: 3px;
}

/*Header subtitle*/
.headline p {
    color: #ffffff;
    font-family: 'Montserrat', sans-serif;
    font-size: 1.17vw;
    letter-spacing: 2.5px;
}

/*-----------------------------Home page description--------------------------*/
/*Description wrapper*/
.description {
    height: 500px;
    margin: auto;
    max-width: 80%;
}

/*Description left block*/
.description-left {
    display: inline-block;
    float: left;
    height:500px;
    padding: 100px 50px 50px 0px;
    width: 50%;
}

/*Left block text*/
.description-left h1 {
    color: #feb14b;
    font-family: 'Montserrat', sans-serif;
    font-size:78px;
    font-weight: 600;
    letter-spacing: 4px;
    line-height:100px;
    padding: 0px, 0px, 50px, 200px;
    text-align: right;   
}

/*Description right block*/
.description-right {
    display: inline-block;
    float: right;
    height:500px;
    line-height:22px;
    padding: 175px 0px 0px 50px;
    width: 50%;
}

/*Right block text*/
.description-right p {
    color: #313638B3;
    font-family: 'Montserrat', sans-serif;
    font-size: 11pt;
    text-align: justify;    
}

/*---------------------------Home page statistics-----------------------------*/

/*Background image*/
.stats-background {
    width: 100%;
}

.stats{
    position: relative;
}

.number {
    display: inline-grid;
    grid-template-columns: 30% 30% 45%;
    left: 0;
    margin: 0 auto;
    place-items: center;
    position: absolute;
    right: 0;
    text-align: center;
    width: 60%;
    bottom: 25%;
}

.number span {
    color: #feb14b;
    font-family: 'Montserrat', sans-serif;
    font-size: 75pt;
    font-weight: 500;
    letter-spacing: 15px;
}

.number p {
    color: #ffffff;
    font-family: 'Montserrat', sans-serif;
    font-size: 14pt;
}

/*-------------------------------Home page events-----------------------------*/
/*Events block*/
.events {
    display: grid;
    grid-template-columns: repeat(5, 20%);
    grid-template-rows: 100px 110px auto auto;
    padding: 100px 0px;
    max-width: 80%;
    margin: 0 auto;
}

/*Events description grid area*/
.events-description {
    grid-area: 1 / 1 / 3 / 3;
}

/*Events description text*/
.events-description p {
    color: #313638B3;
    font-family: 'Montserrat', sans-serif;
    font-size: 11pt;  
    text-align: right;
}

.events-description p#about {
    padding: 20px;
    padding-bottom: 0;
}

.events-description a {
    font-family: 'Montserrat', sans-serif;
    text-align: right;
    color: #e85f5c;
    padding-right: 20px;
}

.events-description a:hover {
    font-family: 'Montserrat', sans-serif;
    text-decoration: none;
    color: #feb14b;
}


/*Events title grid area*/
.events-title {
    text-align: left;
    grid-area: 1 / 3 / 2 / 6;
    padding: 0 20px;
}

/*Big event image grid area*/
.event-1 {
    grid-area: 2 / 3 / 5 / 6;
    padding: 20px;
}

/*Big event image size*/
.event-1 img {
    object-fit: cover;
    width: 100%;
}

/*Small event grid area*/
.event-2 {
    grid-area: 3 / 1 / 5 / 3;
    padding: 20px;
}

/*Small event image size*/
.event-2 img {
    object-fit: cover;
    width: 100%;
}

/*------------------------------Home page sponsors----------------------------*/
/*Sponsors image position*/
.sponsors {
    text-align: center;
    margin: auto;
    padding-top: 20px;
}

