  padding: 0px 0px 0px 30px;
  width: 70%;
  letter-spacing: 2px;
  color: #feb14b;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  text-align: left;
}

.author-name {
  font-family: 'Playfair Display', serif;
  font-size: 20pt;
  margin: 0;
  padding: 0;
}

.position {
  margin-top: 5px;
  font-size: 14pt;
  color: #e85f5c;
}

.author-right p {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  color: #707070;
  font-size: 1vw;
  margin-top: 10px;
}

