/* ---------------------------Sponsor page header-----------------------------*/
/*Header image*/
.sponsorHeader {
    background-image: url('/sponsors-header-1.png');
    background-position: center bottom 40%;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    max-width: 100%;
    overflow: hidden;
    position: relative;
}

/*Heading title*/
.title_area h1 {
    font-family: 'Playfair Display', serif;
    font-size: 5vw;
    letter-spacing: 8px;
    margin-top: 26vw;
    text-transform: uppercase;
}

/*Subheading "Thank you message" & contact*/
p.subheader {
    font-family: 'Montserrat', sans-serif;
    font-size: 12pt;
    color: #313638B3;
    padding: 75px 400px;
    letter-spacing: 2px;
}

/*----------------------------Sponsor page sponsors---------------------------*/
/*Sponsor tier names*/
h1.subsponsor {
    font-size: 38pt;
    letter-spacing: 8px;
    text-transform: uppercase;
    padding: 35px 0px;
}

/*Sponsor logos*/
#majorContainer {
    padding: 0px 175px 50px 175px;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: flex-end;
}

.majorContainer a img {
    margin-bottom: 4vw;
    padding-left: 10px;
    padding-right: 10px;
    
}


/*Sponsor contact*/
a.subheader {
    font-family: 'Montserrat', sans-serif;
    font-size: 1vw;
}

.subheader a:hover {
    color: #feb14b;
    text-decoration: none;
}

p.subheader {
    font-size: 1vw;
    text-align: center;
}