/*-------------------------------Blog post content-----------------------------*/
.blog-content {
  width: 950px;
  text-align: center;
  margin: auto;
  margin-bottom: 6vw;
}

/*Blog title*/
.blog-title {
  font-size: 34pt;
  letter-spacing: 4px;
  margin-top: 8vw;
}

/*Blog subtitle*/
.blog-subtitle {
  font-size: 12pt;
  color: #313638;
  text-align: center;
  margin-bottom: 2vw;
  font-style: italic;
}

/*Blog content*/
.post {
  margin-bottom: 8vw;
  margin: auto;
}

p {
  color: #707070;
  font-family: 'Montserrat', sans-serif;
  font-size: 12pt;
  text-align: justify;
}

b {
  color: #000000;
  font-weight: bolder;
}

p.date {
  color: #313638;
  font-size: 10pt;
  text-align: center;
  margin-bottom: 4vw;
}

.post-content {
  font-weight: 300;
}

.post-heading {
  color: #707070;
  font-size: 24pt;
}

.question {
  font-weight: 500;
}

.list {
  color: #707070;
  font-family: 'Montserrat', sans-serif;
  font-size: 12pt;
  text-align: justify;
  font-weight: 300;
}

.super-emph {
  font-size: 20pt;
  font-weight: bolder;
}

.pic {
  margin: 0 auto;
  max-width: 40%;
  margin-bottom: 4vw;
}

img.floatRight {
  float: right;
  padding: 30px;
}

.post-content a {
  text-decoration: underline;
}

.post-content a:hover{
  color: #feb14b;
}

/*-------------------------------Author card-----------------------------*/
.author {
  margin: 0 auto;
  max-width: 40%;
  margin-bottom: 6vw;
}

/*Author icon*/
.author-left {
  display: inline-block;
  width: 150px;
  height: 150px;
}

.author-left div {
  border-radius: 50%;
  height: 100%;
  overflow: hidden;
  width: 100%
}

.vivw_img {
  height: auto;
  transform: translate( -5%, -10%);
  width: 120%;
}

.vic_img {
  height: auto;
  transform: translate( -5%, -5%);
  width: 120%;
}

/*Author details*/
.author-right {
  display: inline-block;
  padding: 0px 0px 0px 30px;
  width: 70%;
  letter-spacing: 2px;
  color: #feb14b;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  text-align: left;
}

.author-name {
  font-family: 'Playfair Display', serif;
  font-size: 20pt;
  margin: 0;
  padding: 0;
}

.position {
  margin-top: 5px;
  font-size: 14pt;
  color: #e85f5c;
}

.author-right p {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  color: #707070;
  font-size: 1vw;
  margin-top: 10px;
}
